const updateScreenSize = () => {
    const screenHeight = window.innerHeight;
    const screenWidth = window.innerWidth;

    // Log to console
    console.log("Window width: ", screenWidth);
    console.log("Window height: ", screenHeight);

    // Send screen size to Streamlit
    if (window.streamlitComponent) {
        window.streamlitComponent.setComponentValue({ screenHeight, screenWidth });
    }
};

// Call updateScreenSize function on load and resize
window.onload = updateScreenSize;
window.onresize = updateScreenSize;
